import Data from "@/js/core/data";
const Documents_Server = "https://api.${region}.500apps.com/sign/";

class Documents extends Data {
  constructor() {
    super(Documents_Server);
    this.disablePCors();
    this.limit = 1;
  }

  async get_signee_comments(document_id, domain_id, project_id) {
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = `${Documents_Server}comments/${domain_id}/${project_id}/${document_id}`;
    this.url = eval("`" + url + "`");

    return await super.get();
  }

  async add_comment(form) {
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = `${Documents_Server}comment`;
    this.url = eval("`" + url + "`");

    return await super.multi_call(form);
  }
}
export default new Documents();
