<template>
  <div class="px-4">
    <!-- Comments block starts here -->
    <b-row>
      <b-col>
        <b-form @submit.prevent.stop="sendComment">
          <b-card class="mt-4">
            <div v-if="show_comments" class="text-center mb-3">
              <b-spinner label="Spinning small" variant="primary"></b-spinner>
            </div>

            <div v-else-if="comments.length && !show_comments" class="sign-comments">
              <div v-for="(comment, index) in comments" :key="index" class="bg-white rounded mb-3">
                <div class="d-flex align-items-center justify-content-between">
                  <div class="d-flex align-items-start" v-if="get_contact_name(comment.contact_id) && get_contact_name(comment.contact_id).length">
                    <Avatar
                      :username="get_contact_name(comment.contact_id).split(' ')[0]"
                      :rounded="false"
                      class="rounded"
                      :size="25"
                      v-if="get_contact_name(comment.contact_id) && get_contact_name(comment.contact_id).length"
                    />
                    <span class="ml-2 pl-1  text-capitalize line-height-1">{{ get_contact_name(comment.contact_id) }}</span>
                  </div>
                  <span class="small text-muted">{{ get_date(comment.created_date) }}</span>
                </div>
                <div v-html="comment.comments" class="ml-4 pl-3 text-muted comments-section mt-n1" />
              </div>
            </div>
            <div v-else-if="!comments.length && !show_comments" class="bg-white border mb-4 rounded">
              <p class="mb-0 py-4 text-center text-muted">No Comments Found</p>
            </div>
            <label class="text-muted small"> Sender will be able to see the comments posted for the document in the application. Admin will also receive a mail.</label>
            <b-card class="support-editor comments-editor border-0 mt-2" body-class="p-0">
              <quillEditor :options="editorOption" v-model="form.message"></quillEditor>
            </b-card>

            <div class="d-flex align-items-center justify-content-end">
              <span v-if="status_message.length" :class="['mr-3', `badge badge-soft-${status_message.includes('successfully') ? 'success' : 'danger'}`]">{{ status_message }}</span>
              <b-button variant="primary" size="sm" type="submit" class="px-4" :disabled="!form.message">{{ button_message }}</b-button>
            </div>
          </b-card>
        </b-form>
      </b-col>
    </b-row>

    <!-- Comments block ends here -->
  </div>
</template>

<script>
  import CommentJS from "@/js/sign/comments.js";
  import Avatar from "vue-avatar";
  import "quill/dist/quill.snow.css";
  import { quillEditor } from "vue-quill-editor";

  import Quill from "quill";
  // Getting inline styles instead of CSS classes
  var AlignStyle = Quill.import("attributors/style/align");
  Quill.register(AlignStyle, true);
  import moment from "moment";
  export default {
    components: {
      quillEditor,
      Avatar,
    },
    props: { admin: String, show_comments: Boolean, comments: { type: Array, default: () => [] }, project_id: Number, contact_name: String, users: Array },
    data() {
      return {
        button_message: "Send",
        button_disable: false,
        status_message: "",
        form: {
          message: "",
          domain_id: window.domain,
          document_id: this.$route.params.document_id,
          contact_id: this.$route.params.contact_id,
          created_by: window.user,
          project_id: this.project_id,
          contact_name: this.contact_name,
        },
        editorOption: {
          modules: {
            toolbar: [["bold", "italic", "underline"]],
          },
        },
      };
    },
    computed: {
      get_contact_name() {
        return (contact_id) => {
          let user = this.users.find((user) => user.id == contact_id);
          return user ? user?.name : this.admin;
        };
      },
      get_date() {
        return (date) =>
          moment
            .utc(date)
            .local()
            .fromNow();
      },
    },
    methods: {
      async sendComment() {
        let form = { ...this.form };
        this.button_message = "Sending...";

        try {
          let result = await CommentJS.add_comment(this.form);
          if (result?.message) {
            this.$parent.$parent.comments.push({ comments: this.form.message, contact_id: this.$route.params.contact_id, created_date: new Date().toUTCString() });
            this.button_message = "Sent";
            this.status_message = result.message;
            setTimeout(() => {
              this.button_message = "Send";
              this.form.message = this.status_message = "";
            }, 1500);
          }
        } catch (error) {
          if (error) {
            this.form.message = form.message;
            this.button_message = "Send";
            this.status_message = "Failed to sent";
            setTimeout(() => {
              this.status_message = "";
            }, 1500);
          }
        }
      },
    },
  };
</script>
<style>
  @import url("/css/sign/sign.css");
</style>
